body {
  background-image: url("https://img2.goodfon.com/wallpaper/nbig/e/5e/rayman-legends-game-2566.jpg");
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: auto 100%;
  background-repeat: no-repeat;
  z-index: -1;
  
}

#canvas-container {
  position: relative;
  right: 200px;
}

#music-btn {
  position: relative;
  bottom: 0px;
  right: -100px;
  z-index: 30;
}

ul {
    display: flex;
    position: relative;
    bottom: 35px;
    flex-wrap: wrap;
    width: 100px;
    margin: 0px;
    list-style: none;
    z-index: 35;

}

#bridgeleft {
 right: -235px;
}

#bridgeright {
 left: 270px;
}

li:hover {
  background-color: grey; opacity: 0.3;
}

li {
    width: 100px;
    height: 79px;
    margin: 10px transparent;
    border: 1px solid transparent;
    background-color: transparent;
    margin: 2px;
}



#canvas {
  display: flex;
  position: relative;
  z-index: 10;
}

.game-btn {
  color: red;
  font-family: fantasy;
  font-size: x-large;
  position: relative;
  left: 100px;
  bottom: 10px;
  background-color: greenyellow;
  z-index: 20;
}

.game-btn:hover {
  cursor: pointer;
  opacity: 0.5;
}
.game-menu {
  display: flex;
  bottom: 10px;
  z-index: 10;
}

.test {
  display: none;
}


#text {
  background-color: rgb(59, 199, 59, 0.5);

  font-family: Optima, sans-serif;
  font-size: x-large;
  font-weight: bolder;
  color: purple;
  text-align: center;
  position: absolute;
  z-index: 20;
}

#start-btn {
 position: relative;
 left: 250px;
 bottom: 250px; 
 z-index: 30;
}

#music-btn {
  left: 400px;
}
#final {
  font-size: 40px;
}



